import React, { Component } from 'react';
import OrderItem from './orderItem';

class OrderList extends Component {

    showOrderList =(orderList) =>{
        var result = null;
        if(orderList.length >0 ){
            result =  orderList.map((orderItem, index)=> {
                return (
                    <OrderItem
                    key ={index}
                    data ={orderItem}
                    handleDeleteDish = {this.props.handleDeleteDish}
                    />
                )
            })
        }
        return result;
    }

    sendPayment =() => {
        var orderList = this.props.dishpicked;
        if(orderList.length > 0) {
            alert("Đã gửi đơn hàng , Hân hạnh phục vụ quý khách !")
        } else {
            alert("Mời quý khách chọn món !")
        }
    }
    render() {
        var orderList = this.props.dishpicked;
        return (
            <div className="tab_right">
                <div className="thanh-t">
                    <label  id="total-money">Tổng : 3.000.000đ</label>
                    <button id="payment" onClick = {this.sendPayment}><i className="fa fa-credit-card-alt" aria-hidden="true">Send</i></button>
                </div>
                <h2>Ban1 - Tang1</h2>
                {this.showOrderList(orderList)}
            </div>


        );
    }
}

export default OrderList;
