import React, { Component } from 'react';

class OrderItem extends Component {

    onDelete =(id)=> {
        this.props.handleDeleteDish(id);
    }

    render() {
        return (
            <div>
                <ul className="one_dish" >
                    <li className="name_dish pay-item">{this.props.data.name}</li>
                    <li className="price_dish pay-item">{this.props.data.price}</li>
                    <li className="number_dish pay-item"><input type="number" defaultValue={1} name="true"  /></li>
                    <li className="total_one pay-item">100.000đ</li>
                    <li onClick = {this.onDelete.bind(this, this.props.data.id)} className="delete_dish pay-item"><i className="fa fa-trash" aria-hidden="true" /></li>
                </ul>
            </div>
        );
    }
}

export default OrderItem;
